import { NgModule, ApplicationRef } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule } from '@angular/forms';

import { AppComponent } from './app.component';
import { HomeComponent } from "./home/home.component";
import { LoginComponent } from "./login/login.component";
import { PageNotFoundComponent } from "./header/helper/page-not-found.component";
import { MenuNavigationComponent } from "./header/menu/navigation.component";
import { SidebarNavigationComponent } from "./header/sidebar/navigation.component";

import { UserService } from "./services/user.service";
import { AuthGuardService } from "./services/authguard.service";

import { removeNgStyles, createNewHosts } from '@angularclass/hmr';

import { HotModuleReplacementLifecycle } from "./hmr";
import { MenuOptionsComponent } from "./header/menu/options.component";
import { AppRoutingModule } from "./app.routing";
import { NavigationService } from "./services/navigation.service";
import { HelperModule } from "./common/common.module";
import { AppUtils } from "./services/utils";
import { BrowserAnimationsModule } from "@angular/platform-browser/animations";


@NgModule({

  imports: [ BrowserModule, FormsModule, AppRoutingModule, HelperModule, BrowserAnimationsModule ],

  declarations: [
    AppComponent, HomeComponent, LoginComponent, PageNotFoundComponent, MenuOptionsComponent,
    MenuNavigationComponent, SidebarNavigationComponent
  ],

  providers: [ UserService, AuthGuardService, NavigationService, AppUtils ],
  bootstrap: [ AppComponent ]

})
export class AppModule implements HotModuleReplacementLifecycle {

  constructor(public appRef: ApplicationRef) {}

  /**
   * Hot module replacement (HMR) integration
   */

  hmrOnInit(store) {
    console.log('HMR store', store);
  }

  hmrOnDestroy(store) {

    let cmpLocation = this.appRef.components.map(cmp => cmp.location.nativeElement);

    // recreate elements
    store.disposeOldHosts = createNewHosts(cmpLocation);

    // remove styles
    removeNgStyles();
  }

  hmrAfterDestroy(store) {

    // display new elements
    store.disposeOldHosts();
    delete store.disposeOldHosts;
  }
}



