import { Component, AfterViewInit, ViewEncapsulation } from '@angular/core';
import { NavigationMenuItem, NavigationService, NavigationMenuState } from "../../services/navigation.service";

@Component({
  selector: 'app-sidebar-navigation',
  templateUrl: './navigation.component.html',
  encapsulation: ViewEncapsulation.None
})
export class SidebarNavigationComponent implements AfterViewInit {

  readonly home = "Home";
  readonly opportunities = "Opportunities";
  readonly employees = "Employees";
  
  public navigationItems: Array<NavigationMenuItem> = [];
  
  /**
   * primary constructor
   *
   * @param navigationService navigation service, that publishes route state changes, we wish to subscribe to
   */
  constructor( private navigationService: NavigationService ) {}
  
  /**
   * post-init hook
   */
  ngAfterViewInit(): void {

    // subscribe to navigation menu state changes
    this.navigationService.navigationMenu.subscribe( (event: NavigationMenuState) => {
  
      // update menu in template
      this.navigationItems = event.items;
    });
  }
}



