import { Component, AfterViewInit } from '@angular/core';
import { UserService } from "../../services/user.service";
import { NavigationService, NavigationMenuState, NavigationMenuItem } from "../../services/navigation.service";


@Component({
  selector: 'app-menu-navigation',
  templateUrl: './navigation.component.html',
  styleUrls: [ 'navigation.component.scss' ]
})
export class MenuNavigationComponent implements AfterViewInit {

  readonly gisTitle = "GIS";

  navigationItems: Array<NavigationMenuItem> = [];

  constructor( public userService: UserService,
               private navigationService: NavigationService) {}


  ngAfterViewInit(): void {
    
    // subscribe to navigation menu state changes
    this.navigationService.navigationMenu.subscribe( (event: NavigationMenuState) => {

      // update menu in template
      this.navigationItems = event.items;
    });
  }
}



