import { Injectable } from "@angular/core";
import { Router, Event, NavigationEnd } from "@angular/router";
import { Subject, Observable } from "rxjs";


@Injectable()
export class NavigationService {
  
  /**
   * series of events, describing state changes for the main application menu
   */
  private readonly _navigationMenu: Subject<NavigationMenuState> = new Subject();
  
  /**
   * series of events, describing state changes for active route "breadcrumbs"
   */
  private readonly _routeBreadcrumbs: Subject<Array<RouteBreadcrumbItem>> = new Subject();
  
  /**
   * state of the application menu for "/allocation" route
   */
  private readonly allocationNavigationMenu = new NavigationMenuState([
    new NavigationMenuItem("opportunities", "/allocation/opportunities"),
    new NavigationMenuItem("employees", "/allocation/employees"),
    new NavigationMenuItem("clients", "/allocation/clients")
  ]);
  
  /**
   * empty menu state (used when no other routes is active)
   */
  private readonly emptyMenu = new NavigationMenuState([]);
  
  /**
   * public accessor for `_navigationMenu` object, that provides read-only Observable interface
   * (thereby hiding `Subject` implementation class from external access)
   */
  get navigationMenu(): Observable<NavigationMenuState> {
    return this._navigationMenu;
  }
  
  /**
   * public accessor for `_routeParts` object, that provides read-only Observable interface
   * (thereby hiding `Subject` implementation class from external access)
   */
  get routeBreadcrumbs(): Observable<Array<RouteBreadcrumbItem>> {
    return this._routeBreadcrumbs;
  }
  
  /**
   * primary constructor
   *
   * @param router application router service
   */
  constructor( private router: Router ) {
    
    // subscribe to router events
    this.router.events

       // leave only events of `NavigationEnd` type
      .filter( (event: Event) => event instanceof NavigationEnd)
      .subscribe( (navigationEvent: NavigationEnd) => {
        
        // take route url and split it by slash,
        // e.g. from "home/allocation/empolyees" we get ["home", "allocation", "employees"]
        const routeUrlParts = navigationEvent.urlAfterRedirects.split('/').filter( elem => elem && elem !== "");
  
        // parse route url and extract the sequence of "breadcrumbs",
        // e.g. for each route part construct full url of that part and store them together in list:
        // 1) "home" => "/home"
        // 2) "allocation" => "/home/allocation"
        // 3) "employees" => "/home/allocation/employees"
        const routeBreadcrumbs = routeUrlParts.map( (value: string, index: number) =>
          new RouteBreadcrumbItem(value, "/" + routeUrlParts.slice(0, index + 1).join("/") )
        );
        
        // publish new event, containing fresh "breadcrumbs" for newly activated route
        this._routeBreadcrumbs.next(routeBreadcrumbs);
        
        // determine new application menu state and publish it as event, so other UI-components,
        // that listen to this stream of events could correctly update their UI-state
        this._navigationMenu.next( routeUrlParts.indexOf('allocation') > -1 ?
          this.allocationNavigationMenu : this.emptyMenu
        );
        
      });
  }
}

/**
 * immutable event object, that contains the current state snapshot of main navigation menu
 * when the state is changed (e.g. we're moving to another route in application), new event is emmited
 */
export class NavigationMenuState {
  constructor( public readonly items : Array<NavigationMenuItem> ) {}
}

/**
 * immutable data object, describing the state snapshot of each navigation
 */
export class NavigationMenuItem {
  
  /**
   * @param name of the menu item
   * @param url of the menu item
   */
  constructor( public readonly name: string,
               public readonly url: string ) {}
}

/**
 * immutable data object, describing "breadcrumb" of currently activated route
 */
export class RouteBreadcrumbItem {
  
  constructor( public readonly routeName: string,
               public readonly fullUrlPath: string ) {}
}





