import {Injectable} from "@angular/core";
import {List} from "immutable";

@Injectable()
export class AppUtils {

  public splitRecordsByCheckboxes<T>(checkboxes: List<boolean>, records: List<T>): [ List<T>,  List<T>] {

    // split clients on 2 groups - checked and unchecked
    const grouped = records
      .zip(checkboxes)
      .groupBy(pair => pair[1]);

    const recordsToDelete = grouped.get(true) ?
      grouped.get(true).map(pair => pair[0]) : List<T>();

    const unchangedRecords = grouped.get(false) ?
      grouped.get(false).map(pair => pair[0]) : List<T>();

    return [
      recordsToDelete as List<T>,
      unchangedRecords as List<T>
    ];
  }
}


